/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-block-editor/#useBlockProps
 */
import { InspectorControls, useBlockProps } from '@wordpress/block-editor';
import ServerSideRender from '@wordpress/server-side-render';
import {
    TextControl,
    PanelBody,
    PanelRow,
    SelectControl,
    RangeControl
} from '@wordpress/components';
import { useState, useEffect } from '@wordpress/element';
import Devices from './devices';
import { getCategoryOptions } from '../utils/categoryOptions';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @return {WPElement} Element to render.
 */
export default function Edit({ attributes, setAttributes, clientId }) {
    const { 
        categoryID, 
        numberOfItems, 
        orderBy, 
        order, 
        showFeatured, 
        itemDevice,
        desktopSpaceBetween,
        tabSpaceBetween,
        phoneSpaceBetween,
        desktopGridColumn, 
        tabGridColumn, 
        phoneGridColumn 
    } = attributes;
    const blockProps = useBlockProps();

    const instanceId = clientId;
    useEffect(() => {
        setAttributes({ instanceId });
    }, [instanceId]);

    const orderby_options = [
        {
            label: 'Default',
            value: '',
        },
        {
            label: 'Post ID',
            value: 'publish_date',
        },
        {
            label: 'Menu Order',
            value: 'menu_order',
        },
        {
            label: 'Name',
            value: 'title',
        }
    ];

    const order_options = [
        {
            label: 'ASC',
            value: 'ASC',
        },
        {
            label: 'DESC',
            value: 'DESC',
        }
    ];

    const show_featured_options = [
        {
            label: 'No',
            value: '',
        },
        {
            label: 'Yes',
            label: 'yes',
        },
    ]

    const options = getCategoryOptions(wp-fundiJobs);


    return (
        <>
            <InspectorControls>
                {console.log(wp-fundiJobs.taxonomyEnabled)}
                {wp-fundiJobs.taxonomyEnabled === 'true' && (
                    <PanelBody
                        title={__('Category Settings', 'job-notices')}
                        initialOpen={true}
                    >
                        <PanelRow>
                            <fieldset>
                                <SelectControl
                                    label={__('Select a Member Story Category', 'job-notices')}
                                    options={options}
                                    value={categoryID}
                                    onChange={(value) => {
                                        setAttributes({ categoryID: parseInt(value) });
                                    }}
                                />
                            </fieldset>
                        </PanelRow>
                    </PanelBody>
                )}
                <PanelBody
                    title={__('Grid Settings', 'job-notices')}
                    initialOpen={true}
                >
                    <PanelRow>
                        <fieldset>
                            <TextControl
                                label={__('Enter the number of items to display', 'job-notices')}
                                value={numberOfItems}
                                onChange={(value) => setAttributes({ numberOfItems: parseInt(value) })}
                                type="number"
                            />
                        </fieldset>
                    </PanelRow>
                    <Devices
                        device={itemDevice}
                        title={__(
                            'Number of Columns',
                            'job-notices'
                        )}
                        renderFunction={(device) =>
                            setAttributes({
                                itemDevice: device,
                            })
                        }
                    />
                    {itemDevice === 'desktop' && (
                        <RangeControl
                            value={desktopGridColumn}
                            onChange={(desktopGridColumn) =>
                                setAttributes({ desktopGridColumn })
                            }
                            min={1}
                            max={10}
                        />
                    )}
                    {itemDevice === 'tablet' && (
                        <RangeControl
                            value={tabGridColumn}
                            onChange={(tabGridColumn) =>
                                setAttributes({ tabGridColumn })
                            }
                            min={1}
                            max={10}
                        />
                    )}
                    {itemDevice === 'smartphone' && (
                        <RangeControl
                            value={phoneGridColumn}
                            onChange={(phoneGridColumn) =>
                                setAttributes({ phoneGridColumn })
                            }
                            min={1}
                            max={10}
                        />
                    )}

                    <Devices
                        device={itemDevice}
                        title={__(
                            'Space Between Items',
                            'job-notices'
                        )}
                        renderFunction={(device) =>
                            setAttributes({
                                itemDevice: device,
                            })
                        }
                    />
                    {itemDevice === 'desktop' && (
                        <RangeControl
                            value={desktopSpaceBetween}
                            onChange={(desktopSpaceBetween) =>
                                setAttributes({ desktopSpaceBetween })
                            }
                            min={0}
                            max={100}
                        />
                    )}
                    {itemDevice === 'tablet' && (
                        <RangeControl
                            value={tabSpaceBetween}
                            onChange={(tabSpaceBetween) =>
                                setAttributes({ tabSpaceBetween })
                            }
                            min={0}
                            max={100}
                        />
                    )}
                    {itemDevice === 'smartphone' && (
                        <RangeControl
                            value={phoneSpaceBetween}
                            onChange={(phoneSpaceBetween) =>
                                setAttributes({ phoneSpaceBetween })
                            }
                            min={0}
                            max={100}
                        />
                    )}

                    <PanelRow>
                        <fieldset>
                            <SelectControl
                                label={__('Order by', 'job-notices')}
                                options={orderby_options}
                                value={orderBy}
                                onChange={(value) => {
                                    setAttributes({ orderBy: value });
                                }}
                            />
                        </fieldset>
                    </PanelRow>

                    <PanelRow>
                        <fieldset>
                            <SelectControl
                                label={__('Order in', 'job-notices')}
                                options={order_options}
                                value={order}
                                onChange={(value) => {
                                    setAttributes({ order: value });
                                }}
                            />
                        </fieldset>
                    </PanelRow>

                    <PanelRow>
                        <fieldset>
                            <SelectControl
                                label={__('Featured', 'job-notices')}
                                options={show_featured_options}
                                value={showFeatured}
                                onChange={(value) => {
                                    setAttributes({ showFeatured: value });
                                }}
                            />
                        </fieldset>
                    </PanelRow>
                </PanelBody>

            </InspectorControls>
            <div {...blockProps}>
                <ServerSideRender
                    block="job-notices/jobs-grid"
                    attributes={attributes}
                />
            </div>
        </>
    );
}